<template>
  <v-container>
    <section class="border rounded">
      <l-map :zoom="zoomout" :key="mapKey"  :options="mapOptions"  :center="initialCoordinates" height="350px!important" @click="get">

    <l-tile-layer
      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
    ></l-tile-layer>
  
    <l-marker 
      v-for="(marker,i) in markers" 
      :key="i" 
      :lat-lng="marker.latLng"
    >
    <l-tooltip  >
        {{ marker.price }}
        </l-tooltip>
    
      </l-marker>
    <!-- <l-marker :lat-lng="markerCoordinates"></l-marker>

    <l-marker
 key="1"
 :lat-lng="markerCoordinates"
>
</l-marker> -->
  </l-map>
    </section>
   
  </v-container>
  
</template>

<script>
import crudDataService from "@/Services/crudDataService";

import "leaflet/dist/leaflet.css"
import { LMap, LTileLayer, LMarker,LTooltip } from "@vue-leaflet/vue-leaflet";
export default {
  data(){
    return{
      markers:[],
      mapOptions: {
        zoomControl: true,
        scrollWheelZoom: false,
        touchZoom: false,
        doubleClickZoom: true,
        boxZoom: false
      },
      zoomout:5.5,
      mapKey:0,
      initialCoordinates:[24.774265, 46.738586],
      markerCoordinates: [24.774265, 46.738586]
    }
  },
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip
  },
  setup() {
   
  },
  methods: {
  async getallmark(){
    let res=await crudDataService.getAll('ads')
  // Add markers from the 'nearest' array
  res.data.data.sell.nearest.forEach(element => {
    console.log(element); // Logging each element
    this.markers.push({ latLng: [element.lat, element.lng],price:element.price });
  });

  // Add markers from the 'latest' array
  res.data.data.sell.latest.forEach(element => {
    this.markers.push({ latLng: [element.lat, element.lng],price:element.price });
  });
    },
    updateMarkerCoordinates() {
      console.log(this.markerCoordinates);

      const route = this.$route;
      if (route.params.param1 && route.params.param2) {
        // this.markerCoordinates[0] = parseFloat(route.params.param1);
        // this.markerCoordinates[1] = parseFloat(route.params.param2);
        let lat=parseFloat(route.params.param1);
        let lng=parseFloat(route.params.param2);
        this.markerCoordinates=[lat,lng];
        this.initialCoordinates=[lat,lng];
        this.zoomout=14;
        this.mapKey++;
        console.log(this.zoomout);
      console.log(this.markerCoordinates);
      console.log(this.initialCoordinates);
        } else {
          alert("No coordinates provided");
          
      //   console.log('ihwdwdihwoiowqjjo');
      //   console.log(route.params.param1 );
      //   this.markerCoordinates = [
      //  +route.params.param1
      //     ,
      //   +route.params.param2
      //   ]
      //   console.log(this.markerCoordinates);
      }
    },
    get(){
        console.log(LMarker.props.latLng);
    }
  },
  mounted(){
    this.updateMarkerCoordinates();
    this.getallmark()
  }
};
</script>
<style lang="scss">
     .leaflet-container{
    height: 100vh !important;
}
.leaflet-tooltip {
    background-color: #fad76d !important;
    border: 1px solid #000 !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    opacity: 1 !important;
}
.leaflet-tooltip-bottom:before, .leaflet-tooltip-left:before, .leaflet-tooltip-right:before, .leaflet-tooltip-top:before {
 
    border: 0 solid transparent !important;
    border: none !important;
}
</style>