<template>
  <section v-if="items">
    <div class="breadcrumbs mt-10">
      <div
        class="font-weight-bold "
        style="font-size: 40px; cursor: pointer"
        @click="$router.go(-1)"
      >
        {{ $t("Service_providers") }}
      </div>
      <v-icon icon="mdi-slash-forward" style="font-size: 40px"></v-icon>
      <p>{{ items.name }}</p>
    </div>
    <v-row justify="start" class="py-13 row-partner">
      <v-col cols="12" md="2">
        <img
          :src="items.image"
          alt=""
          class="w-100 rounded-circle image-partner"
        />
      </v-col>
      <v-col cols="12" md="10">
        <h2 class="mb-7 font-weight-bold">
          {{ items.name }}
        </h2>
       
        <button
          class="px-2 previewads mb-7 font-weight-medium"
          @click="follow"
          v-if="textchange"
        >
          {{ $t("follow") }}
        </button>
        <button
          class="px-2 previewads mb-5 font-weight-medium"
          @click="unfollow"
          v-else-if="items.is_followed==1"
        >
        {{ $t("unfollow") }}
        </button>

        <p
          v-for="item in items.subscription"
          :key="item.subscription_id"
          class="text-about"
        >
          {{ item.about }}
        </p>
      </v-col>
    </v-row>
    <User_Ads />
  </section>
  <v-row v-else>
    <v-col cols="12" v-for="text in texts" :key="text">
      <v-skeleton-loader
        class="mx-auto border-0"
        type="paragraph"
      ></v-skeleton-loader>
    </v-col>
  </v-row>
</template>
<script>
import User_Ads from "./User_Ads";
import crudDataService from "@/Services/crudDataService";
export default {
  components: {
    User_Ads,
  },
  data() {
    return {
      items: "",
      textchange: "",
      texts: [1, 2, 3, 4,5],
    };
  },
  methods: {
    async SinglePartner() {
      let response = await crudDataService.get(
        `user-profile`,
        `${this.$route.params.id}`
      );
      this.items = response.data.data;
      console.log(this.items);
    },
    async follow() {
      let res = await crudDataService.get(
        `user-follow`,
        `${this.$route.params.id}`
      );

      if (res.data.success) {
        this.textchange = !this.textchange;
      }
      console.log(res.data);

    },
    async unfollow() {
      let res = await crudDataService.get(
        `user-unfollow`,
        `${this.$route.params.id}`
      );
      if (res.data.success) {
        this.textchange = !this.textchange;
      }
      console.log(res.data);
    },
  },
  mounted() {
    this.SinglePartner();
  },
};
</script>
<style lang="scss">
.breadcrumbs {
  display: flex;
  align-items: center;
  p {
    color: #f6c712;
    font-size: 32px;
  }
}
@media (max-width: 550px) {
  .breadcrumbs {
    > div {
      font-size: 22px !important;
    }
    p {
      font-size: 20px !important;
    }
  }
}
</style>
<style lang="scss" scoped>
.text-about {
  font-size: 18px;
  color: #000;
  line-height: 1.8;
  font-weight: 500;
}
.previewads {
  width: 130px;
  height: 45px;
}
@media (max-width: 991.98px) {
  .row-partner {
    justify-content: center !important;
    text-align: center;
    .image-partner {
      width: 30% !important;
    }
  }
}
@media (max-width: 550px) {
  .breadcrumbs {
    > div {
      font-size: 25px !important;
    }
    p {
      font-size: 20px !important;
    }
  }
}
@media (max-width: 350px) {
  .breadcrumbs {
    display: none;
  }
}
</style>
