<template>
  <section v-if="items">
    <div class="text-center my-6">
      <p class="text-h4 font-weight-bold header-page">
        {{ $t("About") }}
      </p>
    </div>

    <div v-html="items" class="text-content text-center"></div>
  </section>

  <v-row v-else >
      
      <v-col cols="12" v-for="text in texts" :key="text">
        <v-skeleton-loader
          class="mx-auto border-0 "
          type="paragraph"
        ></v-skeleton-loader>
      </v-col>
    </v-row>
</template>

<script>
import crudDataService from "@/Services/crudDataService";
export default {
  data() {
    return {
      items: "",
      texts: [1, 2, 3, 4,5],
    };
  },

  methods: {
    async getabout() {
      let response = await crudDataService.getAll("application-data");
      console.log(response.data.data);
      this.items = response.data.data.about_us;
    },
  },
  mounted() {
    this.getabout();
  },
};
</script>

<style>
.header-page {
  color: rgba(17, 24, 39, 1);
}
.text-content{
    font-size: 28px;
    line-height: 1.9;
    font-weight: 500;
}
</style>
