<template>
  <button class="px-2" v-if="isUserLoggedIn" @click="profile">
    <v-icon icon="mdi-account"></v-icon>
    {{ $t("Profile") }}
  </button>
  <button class="px-2" @click="resigt" v-else>
    <v-icon icon="mdi-account-outline"></v-icon>
    {{ $t("LogIn") }}
  </button>
</template>
<script>
import { ref } from "vue";
import { useAuth } from "../../axios";
export default {
  data(){
    return{
      logined:""
    }
  },
  setup() {
    const { isLoggedIn } = useAuth();
    const isUserLoggedIn = ref(isLoggedIn());
    return {
      isUserLoggedIn,
    };
  },
  components: {},
  methods: {
    resigt() {
      this.$router.push({ name: "LogIn" });
    },
    profile() {
      this.$router.push({ name: "Profile" });
    },
  },
  mounted() {
    // this.logined = localStorage.getItem("authToken")?true:false
    // console.log(this.logined);
  },
};
</script>
<style lang="scss" scoped></style>
